import store from '@/store'
import HomeLayout from '@/layouts/homeLayout.vue'

import storeModule from './store'

const Skill = () => import('./containers/skill.vue')

export default {
  path: '/skill',
  component: HomeLayout,
  beforeEnter (to, from, next) {
    if (!store.state.skill) {
      store.registerModule('skill', storeModule)
    }
    next()
  },
  children: [
    {
      path: '',
      name: 'Skill',
      component: Skill
    }
  ]
}
