import store from '@/store'
import HomeLayout from '@/layouts/homeLayout.vue'

import storeModule from './store'

const Experience = () => import('./containers/experience.vue')

export default {
  path: '/experience',
  component: HomeLayout,
  beforeEnter (to, from, next) {
    if (!store.state.experience) {
      store.registerModule('experience', storeModule)
    }
    next()
  },
  children: [
    {
      path: '',
      name: 'Experience',
      component: Experience
    }
  ]
}
