import store from '@/store'
import HomeLayout from '@/layouts/homeLayout.vue'

import storeModule from './store'

const Award = () => import('./containers/award.vue')

export default {
  path: '/award',
  component: HomeLayout,
  beforeEnter (to, from, next) {
    if (!store.state.award) {
      store.registerModule('award', storeModule)
    }
    next()
  },
  children: [
    {
      path: '',
      name: 'Award',
      component: Award
    }
  ]
}
