
const initialState = {
  is_loading: false
}

const getters = {
  getLoadingStatus: state => state.is_loading
}

const actions = {

  setLoadingStatus: ({ commit }, data) => {
    commit('setLoadingStatus', data)
  }
}

const mutations = {

  setLoadingStatus: (state, data) => {
    state.is_loading = data.is_loading
  }
}

export default {
  namespaced: true,
  state: initialState,
  getters,
  actions,
  mutations
}
