import store from '@/store'
import HomeLayout from '@/layouts/homeLayout.vue'

import storeModule from './store'

const Portfolio = () => import('./containers/portfolio.vue')

export default {
  path: '/portfolio',
  component: HomeLayout,
  beforeEnter (to, from, next) {
    if (!store.state.portfolio) {
      store.registerModule('portfolio', storeModule)
    }
    next()
  },
  children: [
    {
      path: '',
      name: 'Portfolio',
      component: Portfolio
    }
  ]
}
