import Vue from 'vue'
import Router from 'vue-router'

import { routes as Home } from '@/scenes/home'
import { routes as Experience } from '@/scenes/experience'
import { routes as Portfolio } from '@/scenes/portfolio'
import { routes as Skill } from '@/scenes/skill'
import { routes as Award } from '@/scenes/award'
import { routes as Contact } from '@/scenes/contact'

import NotFound from '@/scenes/notFoundPage.vue'

Vue.use(Router)

const router = new Router({
  scrollBehavior (to, from, savedPosition) {
    return { x: 0, y: 0 }
  },
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '*',
      name: 'NotFound',
      component: NotFound
    }
  ],
  linkActiveClass: 'active'
})

router.addRoutes([
  Home,
  Experience,
  Portfolio,
  Skill,
  Award,
  Contact
])

export default router
